.header-navigation {
  @include list-style-none();
}
.header__logo-mobail {
  display: none;
}
.header__number {
  display: none;
}
.header__burgermenu {
  display: none;
}

.header-navigation__item {
  display: block;
  text-align: center;
}

.header-navigation__link {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;

  @include font-options(18px, 49px, 500);
  color: $black;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;

  &.js-isActive {
    &::after {
      position: absolute;
      content: "";
      bottom: 10px;
      left: 0;
      width: 100%;
      height: 2px;

      background-color: $black;
    }
  }

  &.js-inFocus {
    color: $black;
  }

  &.js-outFocus {
    color: $grey3;

    &::after {
      background-color: $grey3;
    }
  }

  &:active {
    opacity: .3;
  }
}

@media (max-width: 960px) {
  .header-mobail {
    display: flex;
    height: 76px;
    justify-content: space-between;
    align-items: center;
    padding: 26px 24px 14px;
  }
  .header__logo {
    display: none;
  }
  .header__logo-mobail {
    display: block;
  }
  .mobail__img {
    width: 148px;
    height: 63px;
  }

  .header__number {
    display: block;
    width: 50px;
    height: 50px;
    border: 1px solid #000000;
    margin-right: 11px;
    
  }
  .header__number a {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header__burgermenu {
    display: block;
    width: 50px;
    height: 50px;
    border: 1px solid #000000
  }
  .header__burgermenu-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header__burgermenu-container span {
    display: block;
    width: 24px;
    height: 2px;
    background: #000000;
    margin-bottom: 4px;
    padding: 1px;
    // transform-origin: left center
  }
  .header__mobail-wrapper {
    display: flex;
  }
  .header {
    background: #fff;
    width: 100%;
  }
  .header__travellers-choise  {
    margin: 0 auto!important; 
  }
  .header__burgermenu-container span:nth-child(1) {
    top: 0px;
  }
  .header__burgermenu-container span:nth-child(2) {
    top: 2px;
  }
  .header__burgermenu-container span:nth-child(3) {
    top: 4px;
  }
  .header__burgermenu-container span {
    transform: rotate(0deg);
    transition: .25s ease-in-out;
  }
  .header__burgermenu-container.open {
    transform: rotate(0deg)
  }
  .header__burgermenu-container.open span:nth-child(1) {
    transform: rotate(45deg);
    top: 7px;
    left: 0px;
  }
  .header__burgermenu-container.open span:nth-child(2) {
    width: 0%;
    opacity: 0;
  }
  .header__burgermenu-container.open span:nth-child(3) {
    transform: rotate(-45deg);
    top: -5px;
    left: 0px;
  }
}
