@keyframes preloader {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(100%);
  }
}


.preloader {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  pointer-events: none;

  background-color: $white;
  transition: all 1.5s;

  & ~ .site-wrapper {
    height: 100vh;
    overflow: hidden;
  }

  &.js-isHide {
    opacity: 0;
    visibility: hidden;
  }
}

.preloader__img-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 154px;
  height: 192px;
  margin: 0 auto 85px;
  overflow: hidden;

  & img {
    display: block;
    margin: 0;
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}

.preloader__progress {
  display: block;

  transition: all .35s cubic-bezier(.3,.1,.3,1);
  position: relative;
  overflow: hidden;
  width: 300px;
  height: 1px;

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background-color: $black;
    transform: translateX(-100%);
    animation: preloader 2s cubic-bezier(.75,0,.25,1) infinite;
  }
}
