body,
html {
  margin: 0;
  padding: 0;
  min-height: 100%;
  box-sizing: border-box;
}

*,
*:after,
*:before {
  box-sizing: border-box;
  outline: none;
  -webkit-font-smoothing: antialiased;
}

* {
  position: relative;
}

body {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  overflow-x: hidden;
  min-width: 320px;
  min-height: 100vh;

  font-family: "PF Din Text Cond Pro", "Arial", sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 300;
  color: $black;

  background-color: $white;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
}

::-webkit-scrollbar:vertical {
  width: 3px;
}

::-webkit-scrollbar:horizontal {
  height: 3px;
}

::-webkit-scrollbar-thumb {
  background-color: $black;
  border-radius: 0;
  border: 0;
  transition: all .2s ease-in-out;
}

::-webkit-scrollbar-track {
  background-color: transparent;
  transition: all .2s ease-in-out;
}

a,
label {
  -webkit-tap-highlight-color: transparent;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  @extend %container;
  max-width: 1643px;
}

header,
main,
footer {
  flex-shrink: 0;
}

.fancybox-navigation {
  position: static;
}
