.gallery-instagram {
  @include list-style-none();
  display: grid;
  grid-template-columns: repeat(auto-fit, 273px);
  grid-auto-rows: 1fr;
  gap: 1px;
}

.gallery-instagram__item {
  display: block;
  width: 273px;
  height: 273px;
  margin: 0;
}

.gallery-instagram__link {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: hidden;

  &:hover,
  &:focus {
    .gallery-instagram__img-wrapper {
      transform: scale(1.1);
    }

    .gallery-instagram__info {
      transform: translateY(-100%);
    }
  }
}

.gallery-instagram__img-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;

  transition: all .4s;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.gallery-instagram__info {
  position: absolute;
  width: 100%;
  height: 100%;

  color: $white;
  text-align: center;

  background-color: rgba($black, .7);
  transition: all .4s;
}

.gallery-instagram__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 17px;
  height: 17px;
  margin: 82px auto 12px;

  &::before {
    color: $white;
    font-size: 17px;
  }
}

.gallery-instagram__name {
  display: block;
  width: fit-content;
  margin: 0 auto 27px;

  @include font-options(16px, 19px, 500);
  color: $white;
}

.gallery-instagram__text {
  margin: 0;

  @include font-options(16px, 19px, 300);

  & span {
    display: block;
  }
}
