@import "../elements/temperature";
@import "../elements/footer-navigation";
@import "../elements/sections/gallery-instagram";


.footer {
  width: 100%;
  overflow: hidden;
}

.footer__container {
  display: flex;
  padding-top: 0;
  padding-bottom: 55px;
}

.footer__info {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 50px 0 0;
}

.footer__navigation {
  margin: auto 0 30px;
}

.footer__utility {
  display: flex;
  width: 100%;
  margin: 0 0 68px;
}

.footer__utility-link {
  display: block;
  margin: 0;

  @include font-options(18px, 22px, 300);
  color: $grey;
  text-decoration: underline;

  &:not(:last-child) {
    margin-right: 46px;
  }

  &:hover,
  &:focus {
    color: $black;
  }

  &:active {
    opacity: .6;
  }
}

.footer__copyright {
  margin: 0;

  @include font-options(18px, 22px, 300);
  color: $grey;

  & span {
    display: block;
  }
}

.footer__gallery {
  flex-shrink: 0;
  width: 821px;
}
.footer-about-hotel {
  display: none;
}



@media (max-width: 1800px) {
  .footer__container {
    padding: 120px 55px;
  }
  .gallery-instagram {
    grid-template-columns: repeat(auto-fit, 207px);
}
.gallery-instagram__item {
  width: 200px;
  height: 200px;
}
}

@media (max-width: 1400px) {
  .footer__container {
    display: flex;
    flex-direction: column-reverse;
    padding: 0px 40px;
  }
  .footer__info {
    width: 100%;
  }
  .footer__gallery {
    width: 100%;
  }
  .gallery-instagram {
    grid-template-columns: repeat(auto-fit, 280px);
}
.gallery-instagram__item {
  width: 280px;
  height: 280px;
}
.footer__info {
  padding: 0;
  margin-top: 20px;
}
.footer__navigation {
  margin-top: 40px;
}
.footer-navigation__item:not(:last-child) {
  border-right: 1px solid #000000;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0;
}
.footer-navigation__item:last-child {
  padding-left: 15px;
}
.footer__utility {
  flex-direction: column;
  margin: 0;
  margin-bottom: 25px;
}
.footer__utility-link:not(:last-child) {
  margin-right: 0px;
  margin-bottom: 12px;
}
}

@media (max-width: 960px) {
  .footer__gallery-wrap {
    width: 100%;
    overflow: hidden;
  }
  .gallery-instagram {
    grid-template-columns: none;
    display: flex;
  }
  .footer__gallery-wrap {
    display: flex;
  }
  .gallery-instagram__info {
    transform: translate(0px, 0px);
  }
  .gallery-instagram__name {
    position: absolute;
    bottom: -30px;
    margin: 0;
    color: #000;
  }
  .footer__gallery-wrap {
    overflow: visible;
  }
  .temperature {
    display: none;
  }
  .footer-about-hotel {
    display: block;
    position: relative!important;
    z-index: 0!important;
    padding: 0!important;
    margin-top: 60px;
    .index-about-hotel__curtain {
      position: relative;
    }
  }
}

@media (max-width: 625px) {
    .footer-about-hotel {
      .index-about-hotel__info-description {
        font-size: 16px;
      }
      .index-about-hotel__info-item {
        margin: 0;
      }
      .index-about-hotel__info-title {
        margin: 0;
        margin-bottom: 12px;
      }
      .index-about-hotel__info-button  {
        margin-top: 26px;
      }
    }
}

@media (max-width: 525px) {
  .footer__container {
    padding: 0 10px;
  }
  .footer-navigation__item:not(:last-child) {
    padding-right: 10px;
    padding-left: 10px;
  }
  .footer-navigation__item:last-child {
    padding-left: 10px;
  }
  .footer__utility-link {
    font-size: 16px;
  }
  .footer__copyright {
    font-size: 16px;
  }
}
