.footer-navigation {
  @include list-style-none();
  display: flex;
  width: 100%;
}

.footer-navigation__item {
  display: block;
  margin: 0;

  &:not(:last-child) {
    margin-right: 22px;
  }
}

.footer-navigation__link {
  display: block;
  margin: 0;

  @include font-options(14px, 17px, 500);
  color: $black;
  text-transform: uppercase;
  text-decoration: none;

  &:hover,
  &:focus {
    text-decoration: underline;
  }

  &:active {
    opacity: .6;
  }
}
