@import "../elements/logo";
@import "../elements/header-navigation";
@import "../elements/travellers-choise";
@import "../elements/language";
@import "../elements/socials";


.header {
  display: flex;
  flex-direction: column;
  height: 100vh;
  padding: 26px 24px 14px;
  overflow-y: auto;

  opacity: 0;
}

.header__logo {
  width: 143px;
  height: 179px;
  margin: 0 auto 41px;
}

.header__navigation {
  width: 100%;
  margin: 0 0 28px;
}

.header__travellers-choise {
  width: 79px;
  height: 84px;
  margin: auto auto 32px;
}

.header__language {
  margin: 0 0 16px;
}

.header__pick-up-room-wrapper {
  flex-shrink: 0;
  overflow: hidden;
}

.header__socials {
  margin-top: 19px;
}
