.language {
  width: 100%;
}

.language__list {
  @include list-style-none();
}

.language__item {
  &--active {
   display: none;
  }
}

.language__link {
  display: block;
  width: fit-content;
  margin: 0 auto;

  @include font-options(14px, 42px, 300);
  color: $black;
  text-transform: uppercase;

  &:hover,
  &:focus {
    color: $grey3;
  }

  &:active {
    opacity: .3;
  }
}
