.socials {
  @include list-style-none();

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.socials__item {
  display: block;
  margin: 0 7px;
}

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 37px;
  height: 37px;
  margin: 0;

  &:hover,
  &:focus {
    .socials__icon::before {
      color: $grey3;
    }
  }

  &:active {
    .socials__icon::before {
      opacity: .3;
    }
  }
}

.socials__icon {
  display: block;
  width: 17px;
  height: 17px;
  margin: 0;

  &::before {
    display: block;
    font-size: 17px;
    color: $black;
  }
}
