.scroll-up {
  position: fixed;
  z-index: 5;
  bottom: 55px;
  right: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;

  border-radius: 50%;
  border: none;
  background-color: $sand;
  cursor: pointer;
  transform: scale(0);
  transition: all .4s;

  &.js-isWindowScroll {
    transform: scale(1);

    &:hover,
    &:focus {
      transform: scale(1.3);
    }

    &:active {
      opacity: .6;
    }
  }
}

.scroll-up__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10px;
  height: 10px;
  margin: 0;

  transform: rotate(-90deg);

  &::before {
    font-size: 10px;
    color: $black;
  }
}
