.site-wrapper {
  display: flex;
}

.site-wrapper__left {
  flex-shrink: 0;
  align-self: flex-start;
  position: sticky;
  top: 0;
  width: 277px;
  margin: 0;

  border-right: 1px solid $sand;
}

.site-wrapper__right {
  width: calc(100% - 277px);
  margin: 0;
}

@media (max-width: 960px) {
  .site-wrapper__left {
    width: 185px;
  }
  .site-wrapper__right {
    width: calc(100% - 185px);
  }
}

@media (max-width: 960px) {
  .site-wrapper {
    flex-direction: column;
  }
  .site-wrapper__left {
    width: 100%;
    max-height: 100px;
    z-index: 10;
    background: #fff;
    border-right: 0px;
    position: relative;
  }
  .site-wrapper__right {
    width: 100%;
  }
  .show {
    display: none!important;
  }
}