.temperature {
  @include list-style-none();
  display: flex;
  width: 100%;
}

.temperature__item {
  flex-shrink: 0;
  display: block;
  margin: 0;

  &:not(:last-child) {
    margin-right: 70px;
  }
}

.temperature__location {
  display: block;
  margin: 0 0 18px;

  @include font-options(18px, 22px, 500);
}

.temperature__info {
  display: flex;
}

.temperature__icon {
  display: block;
  width: 29px;
  height: auto;
  margin: 0 12px 0 0;
}

.temperature__value,
.temperature__gauge {
  @include font-options(36px, 43px, 300);
}
